import { useState, useEffect } from "react";
import { BrowserRouter, Routes, Route, HashRouter } from "react-router-dom";
import { Navbar, Feed, Footer, Donate, forms, Admin } from "./components";
import axios from "axios";

import AOS from 'aos';

import 'swiper/css';
import 'aos/dist/aos.css'; // You can also use <link> for style
AOS.init();

const App = () => {
  const [token, setToken] = useState(null);

  function setSessionToken(token) {
    localStorage.setItem("token", token);
    setToken(token);
  }


  function getToken() {
    const tokenString = localStorage.getItem("token");
    const userToken = JSON.parse(tokenString);
    return userToken?._id;
  }

  const [kits, setKits] = useState([]);
  const [news, setNews] = useState([]);
  const [donors, setDonors] = useState([]);
  const [donations, setDonations] = useState([]);

  useEffect(() => {
    getKits();
    getNews();
    getDonors();
    getDonations();

    setToken(getToken());
  }, []);

  async function getKits() {
    try {
      let response = await axios.get("/api/kits");
      setKits(response.data);
    } catch (error) {
      console.log(error);
    }
  }

  async function getNews() {
    try {
      let response = await axios.get("/api/news");
      setNews(response.data);
    } catch (error) {
      console.log(error);
    }
  }

  async function getDonors() {
    try {
      let response = await axios.get("/api/donors");
      setDonors(response.data);
    } catch (error) {
      console.log(error);
    }
  }

  async function getDonations() {
    try {
      let response = await axios.get("/api/donations");
      setDonations(response.data);
    } catch (error) {
      console.log(error);
    }
  }
  
  return (
    <BrowserRouter>
      <div className="bg-white dark:bg-slate-900">
        <Routes>
          <Route path="/" exact element={<Feed kits={kits} news={news} donors={donors} />} />
          <Route path="/donate" exact element={<Donate kits={kits} />} />
          <Route path="/admin/*"
            element={
              token ?
                <Admin
                  kits={kits}
                  setKits={setKits}

                  news={news}
                  setNews={setNews}

                  donors={donors}
                  setDonors={setDonors}

                  donations={donations}
                  setDonations={setDonations}
                />
                :
                <forms.Login
                  setToken={setSessionToken}
                />}
          />
        </Routes>
      </div>
    </BrowserRouter>
  )
}

export default App