/* eslint-disable no-unused-vars */
import React from 'react'
import { useMultistepForm } from './useMultistepForm';
import { FirstStep, SecondStep, ThirdStep, Thanks } from './Steps';

import { Stepper, Step, StepLabel, StepContent, Typography, Alert, IconButton, Collapse } from '@mui/material';
import { Button } from 'flowbite-react';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import CloseIcon from '@mui/icons-material/Close';
import { Route, useNavigate, useNavigation } from 'react-router-dom';
import axios from 'axios';

const initialData = {
    fullName: '',
    email: '',
    phone: '',
    address: '',
    kit: '',
    products: [],
};

/**
 * @typedef {Object} InitialData
 * @property {string} fullName
 * @property {string} email
 * @property {string} phone
 * @property {string} address
 * @property {string} kit
 * @property {{ name: String, checked: Boolean }[]} products
 * @property {string?} message
 */

const Steps = [
    {
        label: "Select a Kit",
    },
    {
        label: "Fill out the form"
    },
    {
        label: "Select kit's products"
    },
]

const ErrorAlert = ({ error }) => {
    const [open, setOpen] = React.useState(true);

    return (
        <Collapse in={open}>
            <Alert
                severity="error"
                sx={{ m: 2 }}
                action={
                    <IconButton
                        aria-label="close"
                        color="inherit"
                        size="small"
                        onClick={() => {
                            setOpen(false);
                        }}
                    >
                        <CloseIcon fontSize="inherit" />
                    </IconButton>
                }
            >
                {error}
            </Alert>
        </Collapse>
    );
}

function Donate({ kits }) {
    const navigate = useNavigate();
    const [data, setData] = React.useState(initialData);
    const [isNextEnabled, setIsNextEnabled] = React.useState(true)
    const [showThanks, setShowThanks] = React.useState(false);
    const [error, setError] = React.useState(null);
    const [errors, setErrors] = React.useState({});
    function updateFields(fields) {
        setData(prev => {
            return { ...prev, ...fields };
        })
    }
    const { step, currentStep, steps, isFirstStep, isLastStep, next, back, jump, reset } = useMultistepForm([
        <FirstStep
            {...data}
            kits={kits}
            updateFields={updateFields}
            setIsNextEnabled={setIsNextEnabled}
            handleNext={handleNext}
        />,
        <SecondStep
            {...data}
            updateFields={updateFields}
            setIsNextEnabled={setIsNextEnabled}
            errors={errors}
            setErrors={setErrors}
        />,
        <ThirdStep
            {...data}
            updateFields={updateFields}
            setIsNextEnabled={setIsNextEnabled}
        />
    ]);


    function handleBack() {
        back();
    }

    function handleNext(e) {
        if (!isLastStep) return next();
        finish();
    }

    React.useEffect(() => {
        if (showThanks) {
            let toRef = setTimeout(() => {
                setShowThanks(false);
                clearTimeout(toRef);
                navigate("/");
            }, 5000);
        }
    }, [showThanks])

    function finish() {
        setIsNextEnabled(false);
        setError(null);
        axios({
            method: 'post',
            url: '/api/donations',
            data: {
                ...data,
                products: data.products.filter(p => p.checked)
            },
        }).then(res => {
            setShowThanks(true);
        }).catch(err => {
            setIsNextEnabled(true);
            setError(err?.response?.data?.message || err?.message || "Error al enviar la donación");
        });
    }


    return (
        <div
            className="bg-[#6EA69B] h-screen"
        >

            {
                error && <ErrorAlert error={error} />
            }
            {showThanks && <Thanks />}
            {/** Top */}
            <div className="flex shadow-md flex-col mb-5 ">
                <div className="flex flex-row items-center">
                    <img
                        src="./logo.png"
                        className="py-1 px-2 h-8 sm:h-12 w-auto"
                        alt="Logo"
                    />
                    <a href="/" className="whitespace-nowrap text-2xl font-semibold font-space dark:text-white text-[#367C2B]">DABYC</a>
                </div>
            </div>

            {/** Steps */}
            <Stepper activeStep={currentStep}>
                {Steps.map((step) => {
                    return (
                        <Step
                            key={step.label}>
                            <StepLabel
                                {...step.props}
                                className='font-bold'

                            >{step.label}</StepLabel>
                            {/* {
                                step.content && (
                                    <StepContent>
                                        <Typography>{step.content}</Typography>
                                    </StepContent>
                                )
                            } */}

                        </Step>
                    )
                })}
            </Stepper>

            {/** Modal */}
            {!showThanks && step}

            {/** Controllers */}
            {
                !showThanks && (
                    <div className="fixed bottom-0 left-0 right-0 z-50 flex justify-between items-center">
                        <Button
                            onClick={handleBack}
                            disabled={isFirstStep}
                            color="dark"
                            outline={true}
                        >
                            <ArrowBackIcon
                                className="mr-2"
                                fontSize="small"
                            />
                            Back
                        </Button>

                        <Button
                            disabled={!isNextEnabled}
                            onClick={handleNext}
                        >

                            {isLastStep ? 'Finish' : 'Next'}
                            <ArrowForwardIcon
                                className="ml-2"
                                fontSize="small"
                            />
                        </Button>
                    </div>
                )
            }
        </div>
    )
}

export default Donate