import { Hero, About, Contact, Footer, Navbar, Sidebar, News, Kits } from "./"
import styles from "../style";
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import TableOfContents from './TableOfContents';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import { useNavigate } from "react-router-dom";
const Feed = ({ kits, news, donors }) => {
    // console.log(kits)
    // const navigate = useNavigate();
    // const [selectedCategory, setSelectedCategory] = useState("New");
    return (
        <>
            <>
                <Element name="navbar" className="element">
                    <Navbar />
                </Element>
                <Element name="home" className="element">
                    <Hero donors={donors} />
                </Element>
                <a className="fixed bottom-1/2 right-0 m-4 cursor-pointer z-50" href="/donate">
                    <div
                        className="border-2 rounded-3xl border-green-900 h-36 w-60 flex items-center flex-col bg-gray-100 shadow-md"

                    >
                        <span className="text-xl p-2 text-center font-bold text-gray-800 bg-green-300 w-full rounded-tl-3xl rounded-tr-3xl">
                            Ayudanos donando
                        </span>
                        <div
                            className="flex flex-row items-center justify-center"
                        >
                            <a
                                href="/donate"
                                className="flex flex-row items-center justify-center pt-4"
                            >
                                <span
                                    className="text-xl p-3 text-center font-bold text-gray-800 bg-emerald-300 w-full rounded-3xl rounded-br-3xl hover:bg-green-400 hover:scale-110 transition duration-500 ease-in-out"
                                >Donar</span>
                            </a>
                        </div>
                    </div>
                </a>
            </>
            <div sx={{ flexDirection: { sx: "column", md: "row" } }}>
                <div
                    p={1}
                    sx={{ overflowY: "auto", height: "90vh", flex: 2 }}
                    className="bg-gradient-to-b from-white via-[#6EA69B] to-[#6EA69B]"
                >
                    <Element name="news">
                        <News news={news} />
                    </Element>
                    <Element name="kits">
                        <Kits kits={kits} />
                    </Element>
                </div>
            </div>

            <Element name="footer">
                <Footer />
            </Element>
        </>
    )
}

export default Feed