import { Modal } from '@mui/material'
import React from 'react'
import { Table } from 'flowbite-react'

function ViewDonation({
    open,
    setOpen,
    data,
    form
}) {

    function handleClose() {
        setOpen(false);
    }

    function formatFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    function isArrayObject(arr) {
        return Array.isArray(arr) && arr.every(item => typeof item === 'object');
    }
    return (
        <Modal
            open={open}
            onClose={handleClose}
            //Enable scroll
            disableScrollLock={false}
        >
            <div
                className="w-full lg:w-1/2 h-auto bg-white rounded-md md:p-5 transform -translate-x-1/2 -translate-y-1/2 absolute top-1/2 left-1/2 flex flex-col md:flex-row items-center md:items-start"
            >

                {/* {
                    form.map(item => item.name).map((item, index) => {
                        let row = true;
                        let value = data[item] || 'N/A';
                        if (isArrayObject(value)) {
                            row = false;
                            let keys = ["name", "quantity"];
                            value =
                                <Table>
                                    <Table.Head>
                                        {
                                            keys.map((item, index) => {
                                                return (
                                                    <Table.HeadCell key={index}>{item}</Table.HeadCell>
                                                )
                                            })
                                        }
                                    </Table.Head>
                                    <Table.Body>
                                        {
                                            value.map((item, index) => {
                                                return (
                                                    <Table.Row key={index}>
                                                        {
                                                            keys.map((key, index) => {
                                                                return (
                                                                    <Table.Cell key={index}>{item[key]}</Table.Cell>
                                                                )
                                                            })
                                                        }
                                                    </Table.Row>
                                                )
                                            })
                                        }
                                    </Table.Body>
                                </Table>
                        }
                        return (
                            <div key={index} className={"flex items-center p-2" + (row ? " flex-row" : " flex-col")}>
                                <p
                                    className={"mx-2 font-bold" + (row ? "" : " text-blue-300 text-2xl")}
                                >
                                    {formatFirstLetter(item)}:
                                </p>
                                {value}
                            </div>
                        )
                    })
                } */}
                <div className="flex flex-col w-full md:w-60">
                    {
                        ["fullName", "email", "phone", "address", "kit", "message"].map((item, index) => {
                            let row = item !== "message";
                            let value = data[item] || 'N/A';
                            return (
                                <div key={index} className={"w-full flex justify-between items-center md:py-5" + (row ? " flex-row" : " flex-col")}>
                                    <p className={"mx-2 font-bold"}>
                                        {formatFirstLetter(item)}:
                                    </p>
                                    <div className="text-gray-800 text-sm">{value}</div>
                                </div>
                            )
                        })
                    }
                </div>
                <div className="flex flex-col w-full md:px-10">
                    {
                        ["products"].map((item, index) => {
                            let keys = ["name", "quantity"];
                            let value = data[item] || [];
                            if (!isArrayObject(value)) return null;
                            return (
                                <Table>
                                    <Table.Head>
                                        {
                                            keys.map((item, index) => {
                                                return (
                                                    <Table.HeadCell key={index}>{item}</Table.HeadCell>
                                                )
                                            })
                                        }
                                    </Table.Head>
                                    <Table.Body>
                                        {
                                            value.map((item, index) => {
                                                return (
                                                    <Table.Row key={index}>
                                                        {
                                                            keys.map((key, index) => {
                                                                return (
                                                                    <Table.Cell key={index}>{item[key]}</Table.Cell>
                                                                )
                                                            })
                                                        }
                                                    </Table.Row>
                                                )
                                            })
                                        }
                                    </Table.Body>
                                </Table>
                            )
                        })
                    }
                </div>
            </div>
        </Modal>
    )
}

export default ViewDonation