import { CircularProgress } from '@mui/material';
import React from 'react';
// import { Kits } from '../constants'

function KitsComponent({ kits }) {
    const [loading, setLoading] = React.useState(true);

    React.useEffect(() => {
        setLoading(true);
        if (Array.isArray(kits) && kits.length > 0) {
            setLoading(false);
        }
    }, [kits]);

    return (
        <div className="bg-[#6EA69B] flex justify-center flex-col items-center w-full">
            <span className="text-[#367C2B] bg-white text-4xl font-semibold w-full text-center">Kits para donaciones</span>
            <div className="flex py-20 flex-wrap justify-center">
                {loading && <CircularProgress />}
                {
                    kits.map((kit, index) => {
                        return (
                            <a
                                className="mx-20 mt-2 flex justify-center items-center flex-col group"
                                href="/donate"
                                key={kit._id}
                            >
                                <img
                                    src={kit.image}
                                    alt={kit.name}
                                    className="w-60 h-60 object-cover object-center rounded-md m-0"
                                ></img>
                                <span className="text-white text-xl font-semibold text-center group-hover:text-gray-800 transition duration-500 ease-in-out">
                                    {kit.name}
                                </span>
                            </a>
                        )
                    })
                }
            </div>
        </div>
    )
}

export default KitsComponent