import { Alert, FormControl, FormHelperText, Input, InputLabel, useFormControl } from '@mui/material'
import React from 'react'
import PropTypes from 'prop-types';
import axios from 'axios';
import { Toast } from 'flowbite-react';

async function loginUser(credentials) {
    return new Promise(async (resolve, reject) => {
        try {
            const response = await axios.post('/api/login', {
                username: credentials.username,
                password: credentials.password
            });
            resolve(response.data);
        } catch (error) {
            reject(error?.response?.data?.error || error.message || error);
        }
    });
    // console.log(credentials);
    // // let response = await axios.post('/api/login', {

    // // }).catch((err) => { console.log(err)});
    // let response = await axios('/api/login', {
    //     method: 'POST',
    //     headers: {
    //         'Content-Type': 'application/json',
    //     },
    //     data: {
    //         username: credentials.username, 
    //         password: credentials.password
    //     },

    // }).catch((err) => { console.log(err) });
    // console.log(response);
    // if (!response) return { error: "Server error" };
    // return response.data;

}



function Login({ setToken }) {
    const [username, setUsername] = React.useState('');
    const [password, setPassword] = React.useState('');
    const [error, setError] = React.useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();
        // const token = await loginUser({
        //     username,
        //     password
        // });
        // console.log(token);
        // if (!token) return;
        // setToken(token);
        try {
            const token = await loginUser({
                username,
                password
            });
            setToken(JSON.stringify(token));
        } catch (error) {
            setError(error);
            console.log(error);
        }
    }

    return (
        <>
            {
                error && <Alert variant="filled" severity="error">
                    {error}
                </Alert>
            }
            <div
                className="flex flex-col items-center justify-center h-screen bg-slate-200"
            >
                <div
                    className="flex flex-col items-center justify-center p-10"
                >
                    <img
                        src="./logo.png"
                        alt="logo"
                        className="w-24 h-24 m-0"
                    />
                    <span className="text-4xl font-bold text-gray-700">Login</span>
                </div>
                {/* <div className="flex flex-col items-center justify-center px-40 border-4 rounded-lg border-blue-400 shadow-2xl p-10 space-y-7"> */}
                <form
                    // className="space-y-4 md:space-y-6"
                    className="flex flex-col items-center justify-center border-4 rounded-lg border-blue-400 shadow-2xl py-12 space-y-4 md:space-y-6 px-20 lg:px-40"
                    onSubmit={handleSubmit}
                >

                    {/* </form> */}
                    <FormControl
                        className='w-64'
                        required
                    >
                        <InputLabel
                            htmlFor="username"
                        >Username
                        </InputLabel>

                        <Input
                            id="username"
                            onChange={e => setUsername(e.target.value)}
                            inputProps={{
                                'aria-label': 'username',
                                pattern: "[A-Za-z0-9]+",
                                title: "Username must be alphanumeric",
                            }}
                        />
                    </FormControl>
                    <FormControl
                        className='w-64'
                        required
                    >
                        <InputLabel
                            htmlFor="password"
                        >Password</InputLabel>

                        <Input
                            id="password"
                            onChange={e => setPassword(e.target.value)}
                            type='password'
                            autoComplete='current-password'
                        />
                    </FormControl>

                    <button
                        type='submit'
                        className="bg-blue-400 hover:bg-blue-500 text-white font-bold py-2 px-4 rounded"
                    >Login</button>
                </form>

            </div>
        </>
    )
}

Login.propTypes = {
    setToken: PropTypes.func.isRequired
}

export default Login