import React from 'react';
import { Alert, Modal } from '@mui/material';
import { Forms } from '../../constants';
import axios from 'axios';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';

function Remove({
    open,
    setOpen,
    index,
    setKits,
    setNews,
    setDonors,
    setDonations,
    data,
    setData,
}) {
    const handleClose = () => {
        if (data._id) setData({});
        setOpen(false);
    };
    const handleClick = async (e) => {
        e.preventDefault();
        let key = Object.keys(Forms)[index];
        let res;
        try {
            res = await axios.delete(`/api/${key}/${data._id}`);
            eval(`set${key.charAt(0).toUpperCase() + key.slice(1)}`)((prev) => prev.filter(item => item._id !== data._id));
            // eslint-disable-next-line no-eval
            handleClose();
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <Modal
            open={open}
            onClose={handleClose}
        >
            <div
                className="w-full md:w-1/2 h-auto bg-white rounded-md p-5 transform -translate-x-1/2 -translate-y-1/2 absolute top-1/2 left-1/2 flex flex-col justify-center items-center"
            >
                <ErrorOutlineIcon
                    sx={{ fontSize: 100 }}
                    color="error"
                />
                <h1 className="text-2xl font-bold text-gray-500 text-center">Estas seguro de eliminar este elemento?</h1>

                <div
                    className="flex justify-center items-center"
                >
                    <button
                        className="m-4 px-4 w-auto bg-red-600 text-white rounded-md hover:bg-red-700"
                        onClick={handleClick}
                    >
                        Si
                    </button>
                    <button
                        className="m-4 px-4 w-auto bg-emerald-500 text-white rounded-md hover:bg-emerald-600"
                        onClick={handleClose}
                    >
                        No, cancelar
                    </button>
                </div>
            </div>
        </Modal>
    );
}

export default Remove;