import { useState } from 'react'

/**
 * 
 * @param {import('react').ReactElement[]} steps 
 * @returns 
 */
export function useMultistepForm(steps) {
    // console.log(steps);
    const [currentStep, setCurrentStep] = useState(0);

    function next() {
        setCurrentStep(i => {
            if (i < steps.length - 1) {
                return i + 1;
            }
            return i;
        });
    }

    function back() {
        setCurrentStep(i => {
            if (i > 0) {
                return i - 1;
            }
            return i;
        });
    }

    /**
     * 
     * @param {Number} step 
     */
    function jump(step) {
        setCurrentStep(step);
    }

    function reset() {
        setCurrentStep(0);
    }

    return {
        currentStep,
        step: steps[currentStep],
        steps,
        isFirstStep: currentStep === 0,
        isLastStep: currentStep === steps.length - 1,
        jump,
        next,
        back,
        reset
    }
}

