import React from 'react';
import { FormWrapper } from './FormWrapper';
import { Kits } from "../../constants"
import { FormControlLabel, FormGroup, Input, LinearProgress } from '@mui/material';
import { Checkbox, Label } from 'flowbite-react';

/**
 * 
 * @param {{ kit: String, products: { name: String, checked: Boolean}[] }}} param0 
 * @returns 
 */
export function ThirdStep({ kit, products, updateFields, setIsNextEnabled }) {
    React.useEffect(() => {
        setIsNextEnabled(products.filter(p => p.checked).length > 0);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [kit, products])

    const handleChange = (product, e) => {
        updateFields({
            products: products.map(p => {
                if (p.name === product.name) {
                    let data = { ...p, checked: e.target.checked };
                    if (e.target.quantity) { 
                        data.quantity = e.target.quantity;
                    }
                    return data;
                }
                return p
            })
        })
    }
    return (
        <FormWrapper title="Select kit's products">
            <div className="flex  justify-center px-6 mx-auto h-auto pt-6 md:mt-0 sm:max-w-md md:max-w-2xl gap-2 flex-col items-center">
                {
                    products.length > 0 ? products
                        .sort((a, b) => a.name.localeCompare(b.name))
                        .map((product, index) => {
                            return (
                                <div className="flex  flex-row items-center w-full justify-between" key={product.name}>
                                    <label
                                        htmlFor="product"
                                        onClick={e => handleChange(product, { target: { checked: !product.checked } })}
                                        className='w-32'
                                    >
                                        <span
                                            className={"text-xl font-space font-bold cursor-pointer select-none text-gray-800"}>
                                            {product.name}
                                        </span>
                                    </label>
                                    <Input
                                        id="product"
                                        type="number"
                                        value={JSON.stringify(product.quantity)}
                                        className="w-20 text-center border-0"
                                        onChange={e => handleChange(product, {
                                            target: {
                                                checked: true,
                                                quantity: parseInt(e.target.value)
                                            }
                                        })}
                                        inputProps={{
                                            min: 1,
                                            max: 100,
                                            pattern: "[0-9]*"
                                        }}
                                    />
                                    <Checkbox
                                        style={{
                                            cursor: "pointer",
                                        }}
                                        id={product.name}
                                        onChange={e => handleChange(product, e)}
                                        checked={product.checked}
                                    />
                                </div>

                            )
                        }) : <div>
                        <LinearProgress color="error" className='w-screen mt-10' />
                        <h1 className="p-10 font-bold text-center text-3xl text-red-400 dark:text-gray-200 my-2">Please select a kit first</h1>
                    </div>
                }
            </div>

        </FormWrapper>
    )
}