import { Box, Tab, Tabs, Typography, Modal, Button } from '@mui/material'
import React from 'react'
import { Admin, Resource } from 'react-admin'
import { kits, Login, authProvider, Create, Remove } from './Forms'
import { Forms } from '../constants'
import { Table } from 'flowbite-react'
import { ViewDonation } from './'

function AdminComponent({
    kits, setKits,
    news, setNews,
    donors, setDonors,
    donations, setDonations,
}) {
    const [value, setValue] = React.useState(0);
    let [formData, setFormData] = React.useState([]);
    const [data, setData] = React.useState({});

    const [create, setCreate] = React.useState(false);
    const [delete_, setDelete] = React.useState(false);
    const [view, setView] = React.useState(false);

    React.useEffect(() => {
        let data = Forms[Object.keys(Forms)[value]] || [];
        setFormData(data);
        setData({});
    }, [value])

    function formatData(data, key = null) {
        let res = {};
        for (let key in data) {
            let value = data[key] || '';

            if (isArrayObject(value)) {
                value = value.map(item => item.name).join(', ');
            }

            if (Array.isArray(value)) {
                value = value.join(', ');
            }
            if (value.length > 25) {
                value = value.slice(0, 25) + "...";
            }
            res[key] = value;
        }
        return res;
    }
    function isArrayObject(arr) {
        return Array.isArray(arr) && arr.every(item => typeof item === 'object');
    }
    return (
        <div className="flex flex-col items-center justify-center">
            <div className="w-screen flex flex-col h-screen lg:flex-row">
                <div className="w-full border-4 rounded-t-2xl lg:rounded-tl-none lg:border-l-0 border-blue-400 p-5 flex flex-row flex-wrap lg:flex-col lg:w-72 justify-center lg:justify-start">
                    {
                        Object.keys(Forms).map((item, index) => {
                            let formattedItem = item.charAt(0).toUpperCase() + item.slice(1)
                            return (
                                <span
                                    key={index}
                                    className=
                                    {
                                        "text-xl font-bold text-gray-700 px-2 py-4 my-1 mx-1 cursor-pointer select-none bg-gray-100 rounded-md"
                                        +
                                        (value === index ? " text-blue-400" : "")
                                        +
                                        " transition duration-200 ease-in-out"
                                    }
                                    onClick={() => setValue(index)}
                                >{formattedItem}</span>
                            )
                        })
                    }
                </div>
                <div className="mt-10 lg:mt-20 w-screen border-y-4 border-blue-400 flex flex-col rounded-tr-2xl">
                    <div className="flex flex-row-reverse h-14 m-2 border-2 bg-gray-100 items-center rounded-md">
                        <button
                            hidden={Object.keys(Forms)[value] === "donations"}
                            className="w-20 h-10 bg-emerald-500 text-white rounded-md"
                            onClick={() => setCreate(true)}
                        >
                            Crear
                        </button>
                    </div>
                    <Table>
                        <Table.Head>
                            {
                                formData.map(a => a.label).map((item, index) => {
                                    return (
                                        <Table.HeadCell>
                                            {item}
                                        </Table.HeadCell>
                                    )
                                })
                            }
                            <Table.HeadCell>
                                <span className="sr-only">
                                    Edit
                                </span>
                            </Table.HeadCell>
                        </Table.Head>
                        <Table.Body className="divide-y">
                            {
                                (eval(Object.keys(Forms)[value]))?.map((item, index) => {
                                    let keys = formData.map(a => a.name);
                                    let formattedData = formatData(item);
                                    return (
                                        <Table.Row
                                            className="bg-white dark:border-gray-700 dark:bg-gray-800"
                                        >
                                            {
                                                keys.map((info, i) => {
                                                    let value = formattedData[info] || "N/A";
                                                    return (
                                                        <Table.Cell>
                                                            {value}
                                                        </Table.Cell>
                                                    )
                                                })
                                            }
                                            <Table.Cell>
                                                <button
                                                    className="font-medium px-4 text-blue-600 hover:underline bg-transparent w-auto h-auto hover:bg-transparent"
                                                    onClick={() => {
                                                        setData(item);

                                                        if (Object.keys(Forms)[value] === "donations") {
                                                            setView(true);
                                                        } else {
                                                            Object.keys(item).forEach(key => {
                                                                if (Array.isArray(item[key])) {
                                                                    item[key] = item[key].join(", ");
                                                                }
                                                            })
                                                            setCreate(true);
                                                        }
                                                    }}
                                                >
                                                    {Object.keys(Forms)[value] === "donations" ? "Ver" : "Editar"}
                                                </button>
                                                <button
                                                    className="font-medium text-red-500 hover:underline bg-transparent w-auto h-auto hover:bg-transparent"
                                                    onClick={() => {
                                                        setData(item);
                                                        setDelete(true);
                                                    }}
                                                >
                                                    Eliminar
                                                </button>
                                            </Table.Cell>
                                        </Table.Row>
                                    )
                                })
                            }
                        </Table.Body>
                    </Table>
                </div>
            </div>

            {/** Create modal */}
            <Create
                open={create}
                index={value}
                data={data}
                form={formData}



                setOpen={setCreate}
                setKits={setKits}
                setNews={setNews}
                setDonors={setDonors}
                setData={setData}
            />

            {/** Remove modal */}
            <Remove
                open={delete_}
                index={value}
                data={data}

                setOpen={setDelete}
                setKits={setKits}
                setNews={setNews}
                setDonors={setDonors}
                setDonations={setDonations}
                setData={setData}
            />

            {/** ViewDonation Modal */}
            <ViewDonation
                open={view}
                setOpen={setView}
                data={data}
                form={formData}
            />
        </div >
    )
}

export default AdminComponent