import React from 'react';
import { Alert, Modal } from '@mui/material';
import { Forms } from '../../constants';
import axios from 'axios';

function Create({
    open,
    setOpen,
    index,
    setKits,
    setNews,
    setDonors,
    data,
    setData,
    form
}) {
    // const [form, setForm] = React.useState(Forms[Object.keys(Forms)[index]]);
    // const [data, setData] = React.useState({});
    const handleClose = () => {
        if (data._id) setData({});
        setOpen(false);
    };
    const handleSubmit = async (e) => {
        e.preventDefault();
        let key = Object.keys(Forms)[index];
        if (data.products && typeof data.products === 'string') {
            data.products = data.products.trim().split(',').map(item => item.trim());
        }
        let res;
        try {
            if (!data._id) {
                res = await axios.post(`/api/${key}`, data);
                eval(`set${key.charAt(0).toUpperCase() + key.slice(1)}`)((prev) => [...prev, res.data]);
            }
            else { 
                res = await axios.put(`/api/${key}/${data._id}`, data);
                eval(`set${key.charAt(0).toUpperCase() + key.slice(1)}`)((prev) => prev.map(item => item._id === data._id ? res.data : item));
            }
            setData({});
            // eslint-disable-next-line no-eval
            handleClose();
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <Modal
            open={open}
            onClose={handleClose}
        >
            {/* <Alert onClose={() => { }}>This is a success alert — check it out!</Alert> */}
            <div
                className="w-full md:w-1/2 h-auto bg-white rounded-md p-5 transform -translate-x-1/2 -translate-y-1/2 absolute top-1/2 left-1/2 flex flex-col"
            >

                <form onSubmit={handleSubmit} >
                    {
                        form.map((item, index) => {
                            return (
                                <div key={index} className="flex flex-col">
                                    <label className="text-gray-700">
                                        {item.label}</label>
                                    <input
                                        type={item.type}
                                        className="border-2 border-gray-300 rounded-md p-2 my-2"
                                        name={item.name}
                                        placeholder={item.placeholder}
                                        required={item.required}
                                        value={data[item.name] || ''}
                                        onChange={(e) => {
                                            setData({
                                                ...data,
                                                [e.target.name]: e.target.value
                                            })
                                        }}
                                        {...item.props}
                                    />
                                </div>
                            )
                        })
                    }
                    <button type="submit" className="my-4 w-20 bg-emerald-500 text-white rounded-md">
                        Enviar
                    </button>
                </form>
            </div>
        </Modal>
    );
}

export default Create;