import { ReactNode } from "react"


/**
 * 
 * @param {{ title: String, children: ReactNode }} param0 
 * @returns 
 */
export function FormWrapper({ title, children }) {
    return (
        <div className="my-10 mx-1 lg:mx-10">
            <h2 className="text-center font-bold tracking-wide text-4xl text-white">{title}</h2>
            <div>{children}</div>
        </div>
    )
}
// export default FormWrapper;