import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y, Controller, EffectFade, Autoplay } from 'swiper';
import { useState } from 'react';
import { assets } from '../constants'
const Hero = ({
    donors
}) => {
    return (

        <section name="hero" id="hero">
            {/* <div className="w-full h-[750px] bg-hero bg-no-repeat bg-center bg-cover"> */}
            {/* <div className="w-full h-80 bg-hero bg-no-repeat bg-center bg-contain lg:h-[750px]">
            </div> */}
            <img
                src={assets.hero}
                alt="hero"
                className="w-full lg:h-[750px] object-contain m-0 lg:object-cover"
            />


            <div className="flex justify-center items-center" name="donantes">
                <div className="container m-10">
                    <Swiper
                        modules={[Navigation, Autoplay]}
                        autoplay={{
                            delay: 5000,
                        }}
                        centeredSlides={true}
                        spaceBetween={30}
                        loop={true}
                        navigation={{
                            nextEl: '.swiper-button-next',
                            prevEl: '.swiper-button-prev',
                            enabled: true,
                        }}
                    >
                        {donors.map((beneficiario, index) => {
                            return (
                                <SwiperSlide
                                    key={index}
                                >
                                    <div className="flex justify-center items-center flex-col">
                                        <img
                                            src={beneficiario.image}
                                            alt=""
                                            className="w-[800px] h-[300px] object-cover object-center shadow-md rounded-lg shadow-slate-400 lg:h-[500px]"
                                        />
                                        {/* <span className="text-2xl font-bold">{beneficiario.name}</span> */}
                                    </div>
                                </SwiperSlide>
                            );
                        })}
                    </Swiper>
                    <div className="flex justify-between">
                        <button className="swiper-button-prev rounded w-24">Prev</button>
                        <button className="swiper-button-next rounded w-24">Next</button>
                    </div>
                </div>
            </div>
        </section >
    )
}

export default Hero