import React from 'react';
import { FormWrapper } from './FormWrapper';
import { Kits } from "../../constants"
import { TextField } from '@mui/material';

export function SecondStep({ fullName, email, phone, address, message, updateFields, setIsNextEnabled, errors, setErrors }) {
    React.useEffect(() => {
        setIsNextEnabled(!!fullName && !!email && !!phone && !!address && !Object.values(errors).some(Boolean));
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [fullName, email, phone, address, errors]);


    const handleChange = (e) => {
        const { name, value } = e.target;
        updateFields({ [name]: value });
    };

    const handleBlur = (e) => {
        const { name } = e.target;
        setErrors(prev => ({
            ...prev,
            [name]: validate(name)
        }));
    };

    /**
     * 
     * @param {String?} key 
     */
    const validate = (key) => {
        const errors = {};
        if (!fullName || fullName.length <= 3) {
            errors.fullName = "Por favor ingrese su nombre completo";
        }
        if (!email) {
            errors.email = "Por favor ingrese su correo electrónico";
        } else if (!/\S+@\S+\.\S+/.test(email)) {
            errors.email = "Por favor ingrese un correo electrónico válido";
        }
        if (!phone) {
            errors.phone = "Por favor ingrese su número de teléfono";
        } else if (!/^\d+$/.test(phone)) {
            errors.phone = "Por favor ingrese un número de teléfono válido";
        }
        if (!address) {
            errors.address = "Por favor ingrese su dirección";
        }
        if (key) {
            return errors[key];
        }
        return errors;
    }

    return (
        <FormWrapper title="Fill out the form">
            <div className="flex flex-col items-center sm:px-6 mx-auto h-auto pt-6 w-full">
                <div className="outline outline-offset-1 outline-blue-500 shadow-inner shadow-gray-300 w-full bg-white rounded-lg dark:border md:mt-0 sm:max-w-md xl:p-0 dark:bg-gray-800 dark:border-gray-700">
                    <div className="p-6 space-y-4 md:space-y-6 sm:p-8">
                        <form className="space-y-4 md:space-y-6">
                            {
                                ["fullName", "email", "phone", "address"].map((field, index) => {
                                    return (
                                        <div className="mb-6 bg-[#F9FAFB]" key={index}>
                                            <TextField
                                                className="bg-[#F9FAFB] border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                                                id={field}
                                                label={field[0].toUpperCase() + field.slice(1).toLowerCase()}
                                                variant="filled"
                                                name={field}
                                                error={!!errors[field]}
                                                helperText={errors[field]}
                                                type={field === "email" ? "email" : field === "phone" ? "tel" : "text"}
                                                // eslint-disable-next-line no-eval
                                                value={eval(field)}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                            />
                                        </div>
                                    )
                                })
                            }
                            <div className="mb-6">
                                <label htmlFor="message" className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-400">Your message</label>
                                <textarea
                                    onChange={handleChange}
                                    name="message"
                                    value={message}
                                    id="message"
                                    rows="4"
                                    className="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                                    placeholder="Leave a comment...">
                                </textarea>
                            </div>
                        </form>
                    </div>

                </div>

            </div>
        </FormWrapper>
    )
}