import React from 'react'
import axios from 'axios';
import { LinearProgress } from '@mui/material';


function News({ news }) {
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState(false);

    React.useEffect(() => {
        setLoading(true);
        if (Array.isArray(news) && news.length > 0) {
            setLoading(false);
        }
    }, [news]);


    return (
        <div className='p-1 md:p-16'>
            {
                loading && <LinearProgress />
            }
            <div className="flex flex-row flex-wrap justify-center">
                {
                    news.map((item, index) => {
                        return (
                            <div
                                key={item._id}
                                // className="w-[500px] bg-white rounded-lg border-4  shadow-lg shadow-black mx-10 my-6 2xl:mx-20 border-gray-700">
                                className="w-[500px] bg-white rounded-lg border-4 shadow-lg shadow-black lg:mx-10 my-6 2xl:mx-20 border-gray-700">
                                {/* <a href="#"> */}
                                <img
                                    className="rounded-t-lg h-72 w-full object-cover object-center m-0"
                                    src={item.image}
                                    alt="" />
                                {/* </a> */}
                                <div className="p-5">
                                    <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">{item.title}</h5>
                                    <p className="mb-3 font-normal text-gray-700 dark:text-gray-400">
                                        {item.description}
                                    </p>
                                    {/* <a href="#" className="inline-flex items-center py-2 px-3 text-sm font-medium text-center text-white bg-blue-700 rounded-lg hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">
                                        Read more
                                        <svg aria-hidden="true" className="ml-2 -mr-1 w-4 h-4" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M10.293 3.293a1 1 0 011.414 0l6 6a1 1 0 010 1.414l-6 6a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-4.293-4.293a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg>
                                    </a> */}
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </div >
    )
}
export default News