import React from 'react';
import { FormWrapper } from './FormWrapper';
// import { Kits } from "../../constants"

export function FirstStep({ kits, kit, products, updateFields, setIsNextEnabled, handleNext }) {
    React.useEffect(() => {
        setIsNextEnabled(!!kit);
    }, [kit, products])

    return (
        <FormWrapper title="Select a Kit">
            <div className="mt-4 container flex flex-row justify-center items-center m-auto flex-wrap  border-2 bg-emerald-50 rounded-lg shadow-2xl">
                {
                    kits.map((k, index) => {
                        return (
                            <div
                                key={index}
                                className={"flex flex-col flex-wrap justify-center items-center p-5 w-80 cursor-pointer"}
                            >
                                <h1 className={"text-2xl font-bold text-center my-2" + (kit === k.name ? " text-emerald-500" : " text-gray-700")}>{k.name}</h1>
                                <img
                                    src={k.image}
                                    alt={k.name}
                                    className={"w-72 h-w-72 object-cover hover:scale-110 transition duration-500 ease-in-out shadow-emerald-100" + (kit === k.name ? " shadow-md" : "")}
                                    onClick={() => {
                                        updateFields({
                                            kit: k.name,
                                            products: k.products.map(p => {
                                                return {
                                                    name: p,
                                                    checked: true,
                                                    quantity: 1
                                                }
                                            })
                                        });
                                        // handleNext();
                                        // setFormValues({
                                        //     ...formValues,
                                        //     kit: kit.name,
                                        //     products: kit.products
                                        // });
                                        // handleNext();
                                    }}
                                />
                            </div>
                        )
                    })
                }
            </div>
        </FormWrapper>
    )
}