import React from 'react'
import confetti from 'canvas-confetti';


const runFireworks = () => {
    var duration = 5 * 1000;
    var animationEnd = Date.now() + duration;
    var defaults = { startVelocity: 30, spread: 360, ticks: 60, zIndex: 0 };

    function randomInRange(min, max) {
        return Math.random() * (max - min) + min;
    }

    var interval = setInterval(function () {
        var timeLeft = animationEnd - Date.now();

        if (timeLeft <= 0) {
            return clearInterval(interval);
        }

        var particleCount = 50 * (timeLeft / duration);
        // since particles fall down, start a bit higher than random
        confetti(Object.assign({}, defaults, { particleCount, origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 } }));
        confetti(Object.assign({}, defaults, { particleCount, origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 } }));
    }, 250);
}

const Thanks = () => {
    React.useEffect(() => {
        runFireworks();
    }, []);

    return (
        <div className="h-screen w-screen fixed bg-white">
            <div className="flex flex-col items-center h-screen w-screen justify-center">
                <img
                    src="./logo.png"
                    className="h-40 w-40 mb-10 object-contain object-center"
                    alt="Logo"
                />
                <h1 className="text-center text-[#0a0b0c] dark:text-gray-200 my-2 capitalize text-4xl font-extrabold">Thank you for your donation!</h1>
                <p className="text-center text-gray-700 dark:text-gray-200 my-2">We will contact you soon.</p>
                <p className="text-center text-gray-700 dark:text-gray-200 my-2">If you have any questions, please contact us</p>
            </div>
        </div>
    )
}




export default Thanks